'use client';

import {
  LineChart,
  Line,
  CartesianGrid,
  XAxis,
  YAxis,
  Tooltip,
  ResponsiveContainer,
} from 'recharts';

import { useEffect, useState } from 'react';
import { API_URL } from '../api/apiURL';

export default function UserTrends() {
  const [userTrendData, setUserTrendData] = useState([]);
  const [funnelData, setFunnelData] = useState([]);

  // Format number like 919.98 K
  const formatK = (num) => (num / 1000).toFixed(2) + ' K';

  // Fetch Trend
  useEffect(() => {
    const fetchTrend = async () => {
      try {
        const res = await fetch(`${API_URL}/admin/user-signup-trends`);
        const result = await res.json();

        if (result.success) {
          const processed = result.data.map((item) => ({
            ...item,
            returningUsers: Math.floor(item.newUsers * 0.5),
          }));

          setUserTrendData(processed);
        }
      } catch (err) {
        console.log(err);
      }
    };
    fetchTrend();
  }, []);

  // Fetch Funnel
  useEffect(() => {
    const fetchFunnel = async () => {
      try {
        const res = await fetch(`${API_URL}/admin/search-to-match`);
        const result = await res.json();
        if (result.success) setFunnelData(result.data);
      } catch (err) {
        console.log(err);
      }
    };
    fetchFunnel();
  }, []);

  return (
    <div className="mt-24 px-6 flex flex-col md:flex-row gap-10 w-full justify-center items-start">

      {/* LEFT: SEARCH TO MATCH */}
      <div className="bg-white border border-gray-300 rounded-[20px] shadow-[0_4px_14px_rgba(0,0,0,0.10)] p-8 w-full md:w-1/2">

        <h2 className="text-[24px] font-bold mb-8">Search To Match</h2>

        <div className="space-y-8">
          {funnelData.map((item, i) => {
            const max = funnelData[0]?.value || 1;
            const percent = (item.value / max) * 100;

            return (
              <div key={i} className="flex items-center">

                {/* LABEL */}
                <div className="w-[165px] text-[18px] text-gray-700 font-medium">
                  {item.stage}
                </div>

                {/* BAR */}
                <div className="flex-1 ml-2 mr-4">
                  <div className="h-[20px] bg-gray-200 rounded-full overflow-hidden">
                    <div
                      className="h-full rounded-[12px]"
                      style={{
                        width: `${percent}%`,
                        background:
                          'linear-gradient(90deg,#3C4BFF 0%,#001BFF 100%)',
                        boxShadow: '0px 3px 8px rgba(0,0,0,0.25)',
                      }}
                    ></div>
                  </div>
                </div>

                {/* VALUE */}
                <div className="w-[80px] text-right text-[18px] font-semibold text-gray-800">
                  {formatK(item.value)}
                </div>

              </div>
            );
          })}
        </div>
      </div>

      {/* RIGHT: RUNNING USER TREND */}
      <div className="bg-white border border-gray-300 rounded-[20px] shadow-[0_4px_14px_rgba(0,0,0,0.10)] p-8 w-full md:w-1/2">

        {/* TITLE + LEGEND */}
        <div className="flex justify-between items-center mb-6">
          <h2 className="text-[26px] font-bold">Running User Trend</h2>

          <div className="flex gap-8 text-[15px] text-gray-700">
            <div className="flex items-center gap-2">
              <span className="h-3 w-3 rounded-full bg-yellow-200 border-2 border-yellow-500"></span>
              <span>New Registered Users</span>
            </div>
            <div className="flex items-center gap-2">
              <span className="h-3 w-3 rounded-full bg-green-200 border-2 border-green-600"></span>
              <span>Returning Users</span>
            </div>
          </div>
        </div>

        {/* CHART */}
        <div className="w-full h-[330px]">
          <ResponsiveContainer width="100%" height="100%">
            <LineChart
              data={userTrendData}
              margin={{ top: 10, right: 20, left: 55, bottom: 10 }}
            >
              <CartesianGrid stroke="#D8D8D8" vertical={false} />

              <XAxis
                dataKey="date"
                tick={{ fontSize: 17 }}
                tickLine={false}
                axisLine={false}
                interval={0}
              />

              <YAxis
                ticks={[0, 5000000, 10000000, 15000000, 20000000, 25000000, 30000000]}
                tickFormatter={(v) => `${v / 1000000} M`}
                tick={{ fontSize: 17 }}
                tickLine={false}
                axisLine={false}
                tickMargin={10}
              />

              <Tooltip />

              {/* YELLOW LINE */}
              <Line
                type="monotone"
                dataKey="newUsers"
                stroke="#facc15"
                strokeWidth={3}
                dot={{
                  r: 7,
                  fill: '#fff',
                  stroke: '#facc15',
                  strokeWidth: 3,
                }}
                activeDot={{ r: 9 }}
              />

              {/* GREEN LINE */}
              <Line
                type="monotone"
                dataKey="returningUsers"
                stroke="#16a34a"
                strokeWidth={3}
                dot={{
                  r: 7,
                  fill: '#fff',
                  stroke: '#16a34a',
                  strokeWidth: 3,
                }}
                activeDot={{ r: 9 }}
              />
            </LineChart>
          </ResponsiveContainer>
        </div>

      </div>
    </div>
  );
}
