'use client';

import React, { useEffect, useState } from 'react';
import Highcharts from 'highcharts';
import HighchartsReact from 'highcharts-react-official';
// 3D plugin browser par chalega automatically when ssr:false
import HC3D from 'highcharts/highcharts-3d';

// enable 3D
if (typeof Highcharts === 'object') {
  HC3D(Highcharts);
}

export default function ProfileMatch() {
  const [profileData] = useState([
    { name: 'Completed', y: 45, color: '#0f8618' },
    { name: 'Low', y: 20, color: '#f39a1e' },
    { name: 'Incomplete', y: 15, color: '#e02020' },
    { name: 'Moderate', y: 20, color: '#f6d21a' },
  ]);

  const [matchesData] = useState([
    { month: 'Jan', totalUsers: 20, matches: 10 },
    { month: 'Feb', totalUsers: 24, matches: 13 },
    { month: 'Mar', totalUsers: 28, matches: 19 },
    { month: 'Apr', totalUsers: 23, matches: 17 },
    { month: 'May', totalUsers: 19, matches: 13 },
    { month: 'Jun', totalUsers: 14, matches: 8 },
    { month: 'Jly', totalUsers: 12, matches: 7 },
    { month: 'Aug', totalUsers: 16, matches: 9 },
    { month: 'Sep', totalUsers: 18, matches: 10 },
    { month: 'Oct', totalUsers: 22, matches: 12 },
    { month: 'Nov', totalUsers: 27, matches: 16 },
    { month: 'Dec', totalUsers: 33, matches: 27 },
  ]);

  const pieOptions = {
    chart: {
      type: 'pie',
      backgroundColor: 'transparent',
      options3d: { enabled: true, alpha: 50 },
      height: 380,
    },
    title: { text: '' },
    plotOptions: {
      pie: {
        depth: 45,
        dataLabels: {
          enabled: true,
          format: '{point.percentage:.1f} %',
          style: { fontSize: '14px', fontWeight: 'bold', textOutline: 'none' },
        },
      },
    },
    series: [{ type: 'pie', data: profileData }],
  };

  const matchOptions = {
    chart: {
      zoomType: 'xy',
      backgroundColor: 'transparent',
      height: 400,
    },
    title: { text: '' },
    xAxis: {
      categories: matchesData.map((x) => x.month),
    },
    yAxis: {
      min: 0,
      max: 35,
      tickInterval: 5,
      labels: { formatter() { return this.value + ' M'; } }
    },
    legend: {
      layout: 'horizontal',
      align: 'right',
      verticalAlign: 'top',
    },
    series: [
      {
        name: 'Total no. of Users',
        type: 'column',
        data: matchesData.map((x) => x.totalUsers),
        color: {
          linearGradient: [0, 0, 0, 300],
          stops: [
            [0, '#ffeb70'],
            [1, '#f4c116']
          ]
        },
        borderRadius: 10,
      },
      {
        name: 'No. Of Matches',
        type: 'line',
        data: matchesData.map((x) => x.matches),
        color: '#e02020',
        lineWidth: 3,
        marker: {
          radius: 6,
          fillColor: '#ffe062',
          lineColor: '#e02020',
          lineWidth: 2
        }
      }
    ]
  };

  return (
    <div className="flex flex-col lg:flex-row gap-6 p-6 w-full">
      
      <div className="bg-white rounded-2xl shadow-xl border p-6 w-full lg:w-[45%]">
        <h2 className="text-2xl font-semibold mb-4">Profile Overview</h2>

        <HighchartsReact highcharts={Highcharts} options={pieOptions} />
      </div>

      <div className="bg-white rounded-2xl shadow-xl border p-6 w-full lg:w-[55%]">
        <h2 className="text-2xl font-semibold mb-4">Matches Per Month</h2>

        <HighchartsReact highcharts={Highcharts} options={matchOptions} />
      </div>

    </div>
  );
}
